.header {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .title {
    color: var(--fg-color);
    font-weight: bolder;
    font-size: 45px;
    // text-transform: uppercase;
  }
  .role {
    color: var(--fg-color-body);
    font-weight: bolder;
    // text-transform: uppercase;
  }
  input[type="checkbox"] {
    width: 0;
    height: 0;
    display: none;
  }
  label {
    cursor: pointer;
    color: var(--fg-color);
  }
}
