@media print {
  @page {
    size: A4;
  }
  .container {
    margin: 0;
    padding: 0;
    box-shadow: none;
    background-color: #ffffff;
    width: 1000px;
    .contacts-block {
      padding: 0;
    }
    .main-block {
      padding: 0;
    }
    .header {
      input,
      label {
        display: none;
      }
    }
  }
}
