.contact {
  margin-bottom: 20px;
  
  .title {
    @include sub-header;
  }
  .contact-item {
    display: flex;
    align-items: center;
    svg {
      width: 20px;
      display: inline-block;
      color: var(--fg-color);
    }
    a {
      text-decoration: none;
      padding-top: 4px;
      padding-left: 4px;
      @include body-text;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
